import Quest from "../models/Quest";

import SongOfTheSeaQuest from "./quests/song_of_the_sea.json";
import ThereAndBackQuest from "./quests/there_and_back.json";

export default class MockQuestService {
    getQuestDetail(id: string): Quest {
        switch (id) {
            case "there_and_back":
                return ThereAndBackQuest;
            case "song_of_the_sea":
            default:
                return SongOfTheSeaQuest;
        }
    }
}
