import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import DoneIcon from "@material-ui/icons/Done";

import InventorySelector from "../InventorySelector";
import Item from "../../models/Item";
import MockPartyService from "../../services/MockPartyService";
import MockItemService from "../../services/MockItemService";

const useStyles = makeStyles({
    itemsButton: {
        margin: "0 10px",
    },
    itemsButtonDiv: {
        width: "100%",
        textAlign: "center",
        paddingBottom: "10px",
    },
});

type PartyInventorySelectProps = {
    onReady: () => void;
};

const PartyInventorySelect: React.FunctionComponent<PartyInventorySelectProps> = (props: PartyInventorySelectProps) => {
    
    const classes = useStyles();
    const [inventoryItems, setInventoryItems] = React.useState<Item[] | null>(null);
    const [isApplied, setIsApplied] = React.useState<boolean>(false);
    const [inventorySelected, setInventorySelected] = React.useState<boolean[]>([]);

    React.useEffect(() => {

        // Initial load of inventory
        const loadItems = () => {
            // TODO: Try / catch
            const partyService = new MockPartyService();
            const inventoryItemIds = partyService.getInventory();

            const itemService = new MockItemService();
            const newInventoryItems = itemService.getItems(inventoryItemIds);

            setInventoryItems(newInventoryItems);
        };

        if (inventoryItems === null) {
            loadItems();
        }
    });

    const inventorySelectionChanged = (id: string) => {
        if (!inventoryItems) return; 
        
        const match = inventoryItems.find((i) => i.id === id);
        if (match === undefined) return;
        const index = inventoryItems.indexOf(match);
        if (index < 0) return;

        const newInventorySelected = inventorySelected.splice(0);
        newInventorySelected[index] = !newInventorySelected[index];
        setInventorySelected(newInventorySelected);
    }

    if (!inventoryItems) return null;

    const inventorySelectedCount = inventorySelected.filter((i) => i === true).length;
    const isInventorySelected = inventorySelectedCount > 0;

    const selectedIndex = inventorySelected.indexOf(true);
    const selectedItem = inventoryItems[selectedIndex];
    
    const isItemSelectable = (): boolean => {
        if (!isInventorySelected) return false;

        return true;
    };

    const onSelectItem = () => {
        if (!isInventorySelected) return;

        //if (onEquipItem) onEquipItem(inventorySelectedIds[0]);

        props.onReady();
        setIsApplied(true);
    };

    const inventoryToolTip = () => {
        if (!isInventorySelected) return "Please select an inventory item";

        return isItemSelectable() ? "Equip the item" : "Please select an equipable item";
    };

    return (
        <div>
            <span>
                <InventorySelector
                    items={inventoryItems}
                    onSelectionChanged={isApplied ? undefined : inventorySelectionChanged}
                    selected={inventorySelected}
                />
            </span>
            <div className={classes.itemsButtonDiv}>
                <Tooltip title={inventoryToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={onSelectItem}
                            disabled={!isInventorySelected || isApplied}
                            variant="contained"
                            color="primary"
                            startIcon={<DoneIcon />}
                        >
                            {isApplied ? "Selected" : "Select"}
                        </Button>
                    </span>
                </Tooltip>
            </div>
        </div>
    );
};

export default PartyInventorySelect;
