import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import MockHeroService from "../../services/MockHeroService";
import HeroList from "../HeroList";

const useStyles = makeStyles({
    container: {
        position: "relative",
        height: "250px",
    },
    partyButton: {
        //margin: "auto 10px",
    },
    partyButtonDiv: {
        width: "100%",
        textAlign: "center",
    },
});

type HeroDismissProps = {
    /** A valid selection has been made */
    onReady: () => void;
};

const HeroDismiss: React.FunctionComponent<HeroDismissProps> = (props: HeroDismissProps): JSX.Element => {
    const classes = useStyles();

    const [heroId, setHeroId] = React.useState("");
    const [isApplied, setIsApplied] = React.useState<boolean>(false);

    const selectionChanged = (ids: string[]) => {
        if (ids.length > 0) setHeroId(ids[0]);
    };

    const isValid = heroId.length > 0;

    const dismissHero = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isValid) return;

        const service = new MockHeroService();

        // TODO: How to trigger a header reload?
        // TODO: Maybe a shared callback?
        props.onReady();
        setIsApplied(true);
    };

    return (
        <div>
            <div className={classes.container}>
                <HeroList chooseAll={true} onSelectionChanged={isApplied ? undefined : selectionChanged} />
            </div>
            <Tooltip title={isValid ? "Dismiss a hero" : "Please select a hero"}>
                <div className={classes.partyButtonDiv}>
                    <Button
                        className={classes.partyButton}
                        onClick={dismissHero}
                        disabled={!isValid || isApplied}
                        variant="contained"
                        color="primary"
                        startIcon={<ExitToAppIcon />}
                    >
                        {isApplied ? "Dismissed" : "Dismiss"}
                    </Button>
                </div>
            </Tooltip>
        </div>
    );
};

export default HeroDismiss;
