import { Choice } from "inkjs/engine/Choice";
import { Story } from "inkjs/engine/Story";
import { IntValue, StringValue } from "inkjs/engine/Value";
import InkStoryEncounter from "../models/InkStoryEncounter";
import InkStoryChoice from "../models/InkStoryChoice";

// Temple dark flame
import TheInterceptContent from "./encounters/temple_dark_flame/TheIntercept.json";
import commonRoomContent from "./encounters/temple_dark_flame/common-room.ink.json";
import chasmContent from "./encounters/temple_dark_flame/chasm.ink.json";
import storeRoomContent from "./encounters/temple_dark_flame/store-room.ink.json";
import dartTrapContent from "./encounters/temple_dark_flame/dart-trap.ink.json";

// Squall's End
import homeContent from "./encounters/squalls_end/home.ink.json";
import nineBellsContent from "./encounters/squalls_end/nine-bells.ink.json";
import brawlContent from "./encounters/squalls_end/brawl.ink.json";
import squallsEndEntryContent from "./encounters/squalls_end/squalls-end-entry.ink.json";
import MockVariableService from "./MockVariableService";
import MockSaveService from "./MockSaveService";

// A mock story service
// Story state will be kept on the server
export default class MockStoryEncounterService {
    private currentStory: Story | null = null;
    private currentId: string | null = null;
    private currentTitle: string = "";

    public startEncounter(id: string): InkStoryEncounter {
        let json: any = undefined;

        switch (id) {
            case "hoard":
            case "wet-room":
            case "intercept":
                json = TheInterceptContent;
                break;
            case "common-room":
                json = commonRoomContent;
                break;
            case "chasm":
                json = chasmContent;
                break;
            case "warrens":
                json = TheInterceptContent;
                break;
            case "home":
                json = homeContent;
                break;
            case "store-room":
                json = storeRoomContent;
                break;
            case "nine-bells":
                json = nineBellsContent;
                break;
            case "brawl":
                json = brawlContent;
                break;
            case "slumberton-entry":
                json = squallsEndEntryContent;
                break;
            case "dart-trap":
                json = dartTrapContent;
                break;
            default:
                json = undefined;
        }

        this.currentStory = json ? new Story(json) : new Story(MockStoryEncounterService.createEmptyStory());
        this.currentId = id;

        const saveService = new MockSaveService();
        const storyStateJson = saveService.getObject<string>("enc_" + this.currentId);

        if (storyStateJson) {
            this.currentStory.state.LoadJson(storyStateJson);
            this.currentStory.state.GoToStart();
        }

        // Extract the Title from the JSON file. It should be in the form "# title: My Encounter"
        const tags = this.currentStory.globalTags;
        const titleTag = tags ? tags.find((t) => t.startsWith("title:")) : undefined;

        this.currentTitle = titleTag ? titleTag.substring(6).trimStart() : "Encounter";

        // Check if there is a skip variable to skip this encounter
        const variableService = new MockVariableService();
        const skipVariableTag = tags ? tags.find((t) => t.startsWith("skip-variable:")) : undefined;
        const skipVariable = skipVariableTag ? skipVariableTag.substring(14).trimStart() : undefined;
        if (skipVariable) {
            const result = variableService.getVariable(skipVariable);
            if (result === "true") {
                 return MockStoryEncounterService.createEmptyStory();
            }
        }

        // Imported local variarbles for use in the encounter
        const imports = tags ? tags.filter((t) => t.startsWith("import:")).map((t) => t.substring(6).trimStart()) : [];
        const importedVariables = imports.join(",").split(",");

        const variableValues = importedVariables.map((v) => [v, variableService.getVariable(v)]).filter((v) => v[1] !== undefined);

        // Assign quest & global variables
        if (this.currentStory != null) {
            variableValues.forEach((v) => (v[0] && v[1] ? this.currentStory?.variablesState.SetGlobal(v[0], new StringValue(v[1])) : null));
        }

        return this.continue();
    }

    public chooseChoice(choiceId: number): InkStoryEncounter {
        if (!this.currentStory || !this.currentId) return MockStoryEncounterService.createEmptyStory();
        this.currentStory.ChooseChoiceIndex(choiceId);

        return this.continue();
    }

    public continue(): InkStoryEncounter {
        if (!this.currentStory || !this.currentId) return MockStoryEncounterService.createEmptyStory();

        if (!this.currentStory.canContinue) {
            const moveTo = this.currentStory.variablesState.GetVariableWithName("moveTo") as StringValue;
            const detailedEncounter = this.currentStory.variablesState.GetVariableWithName("detailedEncounter") as StringValue;
            const coverVisible = this.currentStory.variablesState.GetVariableWithName("coverVisible") as IntValue;
            
            const storyState = this.currentStory.state.toJson(false);

            const saveService = new MockSaveService();
            saveService.saveObject<string>("enc_" + this.currentId, storyState);

            return {
                id: this.currentId,
                title: this.currentTitle,
                paragraphText: "",
                choices: [],
                isFinished: true,
                finishData: {
                    moveTo: moveTo?.value ?? undefined,
                    detailedEncounter: detailedEncounter?.value ?? undefined,
                    coverVisible: coverVisible?.value ?? undefined,
                },
            };
        }

        const paragraphText = this.currentStory.Continue();
        if (!paragraphText) return MockStoryEncounterService.createEmptyStory();

        const encounterChoices: InkStoryChoice[] = this.currentStory.currentChoices.map((c: Choice) => {
            return {
                index: c.index,
                text: c.text,
            } as InkStoryChoice;
        });

        const encounter: InkStoryEncounter = {
            id: this.currentId,
            title: this.currentTitle,
            paragraphText: paragraphText,
            choices: encounterChoices,
            isFinished: false,
        };

        return encounter;
    }

    private static createEmptyStory(): InkStoryEncounter {
        return {
            id: "",
            title: "",
            paragraphText: "",
            choices: [],
            isFinished: true,
        };
    }
}
