import React from "react";
import DoneIcon from "@material-ui/icons/Done";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import HeroList from "../HeroList";

const useStyles = makeStyles({
    container: {
        position: "relative",
        height: "250px",
    },
    partyButton: {
        //margin: "auto 10px",
    },
    partyButtonDiv: {
        width: "100%",
        textAlign: "center",
    },
});

type HeroSelectProps = {
    onSelectionChanged?: (ids: string[]) => void;
    // encounterId: string | null;
    chooseAll?: boolean;
    multiSelect?: boolean;

    /** A valid selection has been made */
    onReady: () => void;
};

/*
* The selection of heroes one the menu bar
*/
const HeroSelector: React.FunctionComponent<HeroSelectProps> = (props: HeroSelectProps) => {
    const classes = useStyles();

    const [heroId, setHeroId] = React.useState<string | undefined>(undefined);
    const [isApplied, setIsApplied] = React.useState<boolean>(false);

    const isValid = heroId !== undefined;

    const changeParty = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isValid) return;

        // TODO: Save the selected hero somehow to the encounter variables?
        props.onReady();
        setIsApplied(true);
    };

    return (
        <div>
            <div className={classes.container}>
                <HeroList chooseAll={props.chooseAll} multiSelect={props.multiSelect} onSelectionChanged={props.onSelectionChanged} />
            </div>
            <Tooltip title={isApplied ? "Hero selected" : isValid ? "Hero selected" : "Please select a hero"}>
                <div className={classes.partyButtonDiv}>
                    <Button
                        className={classes.partyButton}
                        onClick={changeParty}
                        disabled={!isValid || isApplied}
                        variant="contained"
                        color="primary"
                        startIcon={<DoneIcon />}
                    >
                        {isApplied ? "Selected" : "Select"}
                    </Button>
                </div>
            </Tooltip>
        </div>
    );
}

export default HeroSelector;
