import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";

import InventorySelector from "./InventorySelector";
import Item from "../models/Item";

const useStyles = makeStyles({
    itemsButton: {
        margin: "0 10px",
    },
    itemsButtonDiv: {
        width: "100%",
        textAlign: "center",
        paddingBottom: "10px",
    },
});

type PartyInventoryProps = {
    items: Item[];
    onEquipItem: (itemId: string) => void;
};

const PartyInventoryEquip: React.FunctionComponent<PartyInventoryProps> = (props: PartyInventoryProps) => {
    const classes = useStyles();

    const { items, onEquipItem } = props;
    
    const [inventorySelected, setInventorySelected] = React.useState<boolean[]>([]);

    const inventorySelectionChanged = (id: string) => {
        const match = items.find((i) => i.id === id);
        if (match === undefined) return;
        const index = items.indexOf(match);
        if (index < 0) return;
        
        const newInventorySelected = inventorySelected.splice(0);
        newInventorySelected[index] = !newInventorySelected[index];
        setInventorySelected(newInventorySelected);
    };

    const inventorySelectedCount = inventorySelected.filter((i) => i === true).length;
    const isInventorySelected = inventorySelectedCount > 0;

    const selectedIndex = inventorySelected.indexOf(true);
    const selectedItem = items[selectedIndex];

    const isItemEquipable = (): boolean => {
        if (!isInventorySelected) return false;
        return selectedItem.itemType >= 10;
    };

    const equipItem = () => {
        if (!isInventorySelected) return;
        if (onEquipItem) onEquipItem(selectedItem.id);
    };

    const inventoryToolTip = () => {
        if (!isInventorySelected) return "Please select an inventory item";

        return isItemEquipable() ? "Equip the item" : "Please select an equipable item";
    };

    return (
        <div>
            <span>
                <InventorySelector items={items} onSelectionChanged={inventorySelectionChanged} selected={inventorySelected} />
            </span>
            <div className={classes.itemsButtonDiv}>
                <Tooltip title={inventoryToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={equipItem}
                            disabled={!isInventorySelected}
                            variant="contained"
                            color="primary"
                            startIcon={<ArrowUpwardIcon />}
                        >
                            Equip
                        </Button>
                    </span>
                </Tooltip>
            </div>
        </div>
    );
};

export default PartyInventoryEquip;
