import React from "react";
import { makeStyles, List, ListItem, ListItemAvatar, Avatar, ListItemText, Grid } from "@material-ui/core";

import HeroTrait from "../models/character/HeroTrait";

import FlashOnIcon from "@material-ui/icons/FlashOn";
import FaceIcon from "@material-ui/icons/Face";
import AccessibilityNewIcon from "@material-ui/icons/AccessibilityNew";
import AllInboxIcon from "@material-ui/icons/AllInbox";

const useStyles = makeStyles({
    selected: {
        margin: "5px",
        backgroundColor: "lightblue",
        borderRadius: "4px",
        border: "solid steelblue 4px",
    },
    notSelected: {
        margin: "5px",
        borderRadius: "4px",
        border: "solid white 4px",
    },
});

type TraitListProps = {
    traits: HeroTrait[];
    onSelect?: (trait: HeroTrait) => void;
};

const TraitList: React.FunctionComponent<TraitListProps> = (props: TraitListProps) => {
    const classes = useStyles();

    const { traits } = props;
    const [selectedTrait, setSelectedTrait] = React.useState<HeroTrait | undefined>(undefined);

    const renderTraitIcon = (t: HeroTrait) => {
        switch (t.type) {
            case 1: // Mental
                return <FaceIcon />;
            case 2: // Mystical
                return <FlashOnIcon />;
            case 3: // Physical
                return <AccessibilityNewIcon />;
            case 4: // Item
                return <AllInboxIcon />;
            default:
                return <AllInboxIcon />;
        }
    };

    const onSelect = (trait: HeroTrait) => {
        if (props.onSelect) {
            console.log("select : " + trait.id);
            if (selectedTrait === undefined || trait.id !== selectedTrait.id) {
                setSelectedTrait(trait);
                props.onSelect(trait);
            }
        }
    };

    const renderTrait = (t: HeroTrait) => (
        <ListItem
            key={t.id}
            className={selectedTrait !== undefined && t.id === selectedTrait.id ? classes.selected : classes.notSelected}
            onClick={() => onSelect(t)}
        >
            <ListItemAvatar>
                <Avatar>{renderTraitIcon(t)}</Avatar>
            </ListItemAvatar>
            <ListItemText primary={t.name} secondary={t.from ?? t.description} />
        </ListItem>
    );

    return <List dense={true}>{traits.map((t) => renderTrait(t))}</List>;
};

export default TraitList;
