import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import ArrowDownwardIcon from "@material-ui/icons/ArrowDownward";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";

import InventorySelector from "../InventorySelector";
import Item from "../../models/Item";

const useStyles = makeStyles({
    lootImg: {
        width: "25px",
        position: "absolute",
        top: "220px",
        left: "100px",
    },
    inventoryImg: {
        width: "25px",
        position: "absolute",
        top: "420px",
        left: "100px",
    },
    itemsButton: {
        //margin: "auto 10px",
        margin: "0 10px",
    },
    itemsButtonDiv: {
        width: "100%",
        textAlign: "center",
        paddingBottom: "10px",
    },
});

type ItemSelectProps = {
    /** The item list */
    items: Item[];

    /** The inventory items */
    inventory: Item[];

    /** True to indicate stash in UI */
    useStash: boolean;

    /** Item was removed from inventory */
    onPut: (itemIds: string[]) => void;

    /** Item was added to party inventory */
    onTake: (itemIds: string[]) => void;
};

const ItemSelect: React.FunctionComponent<ItemSelectProps> = (props: ItemSelectProps) => {
    const classes = useStyles();

    const { items, inventory, useStash } = props;

    // TODO: Move to use indexes!! Otherwise we lose track of item doubles with the same ID
    const [itemsSelected, setItemsSelected] = React.useState<boolean[]>([]);
    const [inventorySelected, setInventorySelected] = React.useState<boolean[]>([]);

    const maxItems = 8;

    const itemSelectionChanged = (id: string) => {
        const match = items.find(i => i.id === id);
        if (match === undefined) return;
        const index = items.indexOf(match);
        if (index < 0) return;

        // Use a new array to leat react know that it changed
        const newItemsSelected = itemsSelected.splice(0);
        newItemsSelected[index] = !newItemsSelected[index];
        setItemsSelected(newItemsSelected);
    };

    const inventorySelectionChanged = (id: string) => {
        const match = inventory.find((i) => i.id === id);
        if (match === undefined) return;
        const index = inventory.indexOf(match);
        if (index < 0) return;

        // Use a new array to leat react know that it changed
        const newInventorySelected = inventorySelected.splice(0);
        newInventorySelected[index] = !newInventorySelected[index];
        setInventorySelected(newInventorySelected);
    };

    const itemsSelectedCount = itemsSelected.filter(i => i === true).length;
    const inventorySelectedCount = inventorySelected.filter((i) => i === true).length;

    const isItemSelected = itemsSelectedCount > 0;
    const isInventorySelected = inventorySelectedCount > 0;

    const willItemsBeTooBig = (): boolean => items.length + inventorySelectedCount > maxItems;
    const willInventoryBeTooBig = (): boolean => inventory.length + itemsSelectedCount > maxItems;

    const deselectAll = () => {
        setItemsSelected([false, false, false, false, false, false, false, false]);
        setInventorySelected([false, false, false, false, false, false, false, false]);
    };

    const takeItem = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isItemSelected) return;

        const takeItems: string[] = [];
        for (let i = 0; i < 8; i++) {
            if (itemsSelected[i]) {
                takeItems.push(items[i].id);
            }
        }
        props.onTake(takeItems);
        deselectAll();
    };

    const putItem = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isInventorySelected) return;

        const putItems: string[] = [];
        for (let i = 0; i < 8; i++) {
            if (inventorySelected[i]) {
                putItems.push(inventory[i].id);
            }
        }
        props.onPut(putItems);
        deselectAll();
    };

    const lootSourceName = useStash ? "stash" : "loot";

    const itemToolTip = () => {
        if (willInventoryBeTooBig()) return "Too many items in inventory";

        return isItemSelected ? `Take item from the ${lootSourceName}` : `Please select a ${lootSourceName} item`;
    };

    const inventoryToolTip = () => {
        if (willInventoryBeTooBig()) return `Too many items in ${lootSourceName}`;

        return isInventorySelected ? `Put item in the ${lootSourceName}` : "Please select an inventory item";
    };

    return (
        <div>
            <span>
                <img src={`./images/${lootSourceName}.png`} alt={lootSourceName} className={classes.lootImg} />
                <InventorySelector items={items} multiSelect={true} onSelectionChanged={itemSelectionChanged} selected={itemsSelected} />
            </span>
            <div className={classes.itemsButtonDiv}>
                <Tooltip title={itemToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={takeItem}
                            disabled={willInventoryBeTooBig() || !isItemSelected}
                            variant="contained"
                            color="primary"
                            startIcon={<ArrowDownwardIcon />}
                        >
                            Take
                        </Button>
                    </span>
                </Tooltip>
                <Tooltip title={inventoryToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={putItem}
                            disabled={willItemsBeTooBig() || !isInventorySelected}
                            variant="contained"
                            color="primary"
                            startIcon={<ArrowUpwardIcon />}
                        >
                            Put
                        </Button>
                    </span>
                </Tooltip>
            </div>
            <span>
                <img src="./images/inventory.png" alt="inventory" className={classes.inventoryImg} />
                <InventorySelector
                    items={inventory}
                    multiSelect={true}
                    onSelectionChanged={inventorySelectionChanged}
                    selected={inventorySelected}
                />
            </span>
        </div>
    );
};

export default ItemSelect;
