import React from "react";
import { makeStyles } from "@material-ui/core";

import MockPartyService from "../../services/MockPartyService";
import MockItemService from "../../services/MockItemService";
import Item from "../../models/Item";
import ItemSelect from "./ItemSelect";

const useStyles = makeStyles({
});

type ItemSelectorProps = {
    /** The trait selection list */
    itemIds: string[];

    /** Use a luck roll to choose an item */
    isRandom: boolean;

    /** Can select more than one item */
    selectMultiple: boolean;

    /** A valid selection has been made */
    onReady: () => void;
};

const ItemLoot: React.FunctionComponent<ItemSelectorProps> = (props: ItemSelectorProps): JSX.Element | null => {
    const classes = useStyles();

    const [lootItems, setLootItems] = React.useState<Item[] | null>(null);
    const [inventoryItems, setInventoryItems] = React.useState<Item[] | null>(null);

    React.useEffect(() => {
        // Initial load of inventory
        const loadLootItems = () => {
            // TODO: Try / catch

            const itemService = new MockItemService();
            const newStashItems = itemService.getItems(props.itemIds);

            setLootItems(newStashItems);
        };

        const loadInventoryItems = () => {
            // TODO: Try / catch
            const partyService = new MockPartyService();
            const inventoryItemIds = partyService.getInventory();

            const itemService = new MockItemService();
            const newInventoryItems = itemService.getItems(inventoryItemIds);

            setInventoryItems(newInventoryItems);
        };

        if (lootItems === null) {
            loadLootItems();
        }

        if (inventoryItems === null) {
            loadInventoryItems();
        }
    });

    const takeItem = (itemIds: string[]) => {
        if (!lootItems || !inventoryItems) return;

        const service = new MockPartyService();
        for (let i = 0; i < itemIds.length; i++) {
            //service.removeStashItem(itemIds[i]);
            service.addInventoryItem(itemIds[i]);
        }

        // TODO: Doesn't work for duplicate item names
        const takenItems = lootItems.filter((i) => itemIds.indexOf(i.id) >= 0);
        setLootItems(lootItems.filter(i => itemIds.indexOf(i.id) < 0));
        setInventoryItems(inventoryItems.concat(takenItems));
    };

    const putItem = (itemIds: string[]) => {
        if (!lootItems || !inventoryItems) return;

        const service = new MockPartyService();
        for (let i = 0; i < itemIds.length; i++) {
            service.removeInventoryItem(itemIds[i]);
        }

        // TODO: Doesn't work for duplicate item names
        const putItems = inventoryItems.filter((i) => itemIds.indexOf(i.id) >= 0);
        setLootItems(lootItems.concat(putItems));
        setInventoryItems(inventoryItems.filter((i) => itemIds.indexOf(i.id) < 0));
    };

    if (!lootItems || !inventoryItems) return null;

    return <ItemSelect items={lootItems} inventory={inventoryItems} onPut={putItem} onTake={takeItem} useStash={false} />;
};

export default ItemLoot;
