import Trait from "../models/character/Trait";

import CompulsionKleptomaniac from "./traits/compulsion_kleptomaniac.json";
import PhobiaUndead from "./traits/phobia_undead.json";
import ResurrectionBotched from "./traits/resurrection_botched.json";
import ToughSkin from "./traits/physical_tough_skin.json";

export default class MockTraitService {
    getTraitDetail(id: string): Trait {
        switch (id) {
            case "phobia_undead":
                return PhobiaUndead;
            case "physical_tough_skin":
                return ToughSkin;
            case "resurrection_botched":
                return ResurrectionBotched;
            case "compulsion_kleptomaniac":
            default:
                return CompulsionKleptomaniac;
        }
    }

    getTraits = (ids: string[]): Trait[] => ids.map((id) => this.getTraitDetail(id));
}
