import React from "react";
import { Grid, makeStyles, Tooltip, Button } from "@material-ui/core";

import MockTraitService from "../../services/MockTraitService";
import Trait from "../../models/character/Trait";
import TraitList from "../TraitList";
import HeroTrait from "../../models/character/HeroTrait";
import HeroList from "../HeroList";
import MockHeroService from "../../services/MockHeroService";

const useStyles = makeStyles({
    container: {
        //position: "relative",
    },
    party: {
        padding: "0 30px",
        width: "90%",
    },
    traits: {
        padding: "0 30px",
        width: "90%",
    },
    hero: {
        width: "70px",
        height: "70px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "lightgrey",
        margin: "4px",
        cursor: "pointer",
    },
    heroSelected: {
        width: "70px",
        height: "70px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "mediumaquamarine",
        margin: "4px",
        cursor: "pointer",
    },
    traitsBlock: {
        padding: "10px 0",
    },
    button: {
        //margin: "auto 10px",
    },
    buttonDiv: {
        width: "100%",
        textAlign: "center",
    },
});

type TraitSelectProps = {
    /** The trait selection list */
    traitIds: string[];

    /** Where is the trait from */
    from: string;

    /** Use a luck roll to choose a trait */
    isRandomTrait: boolean;

    /** A valid selection has been made */
    onReady: () => void;
};

/*
 * Select a trait, or just give a trait to a hero
 */
const TraitSelect: React.FunctionComponent<TraitSelectProps> = (props: TraitSelectProps) => {
    const classes = useStyles();
    const { traitIds } = props;

    const [traits, setTraits] = React.useState<Trait[] | undefined>(undefined);
    const [heroId, setHeroId] = React.useState<string | undefined>(undefined);
    const [selectedTrait, setSelectedTrait] = React.useState<string | undefined>(undefined);
    const [isApplied, setIsApplied] = React.useState<boolean>(false);

    React.useEffect(() => {
        // Initial load of traits
        const loadTraits = () => {
            const service = new MockTraitService();
            const newTraits = service.getTraits(traitIds);
            if (!newTraits) {
                console.error(`No trait data`);
                return;
            }

            setTraits(newTraits);
        };

        if (traits == undefined) {
            loadTraits();
        }
    });

    const onHeroSelect = (ids: string[]) => {
        if (ids.length > 0) {
            setHeroId(ids[0]);
        }
    };

    const onSelect = (trait: Trait) => {
        // TODO: Assign the trait to the selected hero?
        setSelectedTrait(trait.id);
    };

    const renderTraits = () => {
        if (!traits) return null;

        return (
            <Grid container className={classes.traitsBlock}>
                <Grid item xs={1}></Grid>
                <Grid item xs={10}>
                    <TraitList onSelect={isApplied ? undefined : onSelect} traits={traits.map((t) => t as HeroTrait)} />
                </Grid>
                <Grid item xs={1}></Grid>
            </Grid>
        );
    };

    const isValid = selectedTrait !== undefined && heroId !== undefined;

    const onApply = () => {
        if (heroId && selectedTrait) {
            var service = new MockHeroService();
            service.addPermanentTrait(heroId, selectedTrait, props.from);
            props.onReady();
            setIsApplied(true);
        }
    };

    return (
        <div className={classes.container}>
            <div className={classes.party}>
                <HeroList onSelectionChanged={isApplied ? undefined : onHeroSelect} />
            </div>
            <div className={classes.traits}>
                <Grid container>{renderTraits()}</Grid>
            </div>
            <Tooltip title={isApplied ? "Trait has been applied" : isValid ? "Apply this trait to the hero" : "Select a trait and a hero"}>
                <div className={classes.buttonDiv}>
                    <Button
                        className={classes.button}
                        onClick={onApply}
                        disabled={!isValid || isApplied}
                        variant="contained"
                        color="primary"
                    >
                        {isApplied ? "Applied" : "Apply"}
                    </Button>
                </div>
            </Tooltip>
        </div>
    );
};

export default TraitSelect;
