import React from "react";

// Models
import GameState from "../models/GameState";
import { MapGraph } from "../models/MapGraph";

// Services
import MockMapService from "../services/MockMapService";
import GraphBuilder from "../services/GraphBuilder";
import OverheadMap from "./OverheadMap";
import LoadingProgress from "./LoadingProgress";

type OverheadMapProps = {
    startState: GameState;
    onNewMap: (state: GameState) => void; // Fire when a new map load is needed
};

const OverheadMapLoader: React.FunctionComponent<OverheadMapProps> = (props: OverheadMapProps) => {
    const { startState } = props;

    const [graph, setGraph] = React.useState<MapGraph | undefined>(undefined); // The map graph data
    const [pathColor, setPathColor] = React.useState<string | undefined>(undefined);

    const [mapUrl, setMapUrl] = React.useState<string | undefined>(undefined);
    const [coverUrls, setCoverUrls] = React.useState<string[]>([]);
    const [useDayNight, setUseDayNight] = React.useState<boolean>(false);

    React.useEffect(() => {
        // Initial load of map data
        const loadMap = () => {
            const service = new MockMapService();

            const data = service.getMap(startState.currentMapId);
            if (!data) {
                console.error(`No map data`);
                return;
            }

            console.log(
                `Loaded map : ${startState.currentMapId}, ${startState.currentVertextId}. ${data?.vertices.length} vertices. Path Color ${
                    data.pathColor ?? "None"
                }`
            );

            setMapUrl(data.imageUrl);
            setCoverUrls(data.coverUrls);
            setPathColor(data.pathColor ?? "#5E4E00"); // The default path colour is brown
            setUseDayNight(data.useDayNight);

            const mapGraph = GraphBuilder.Build(data);
            setGraph(mapGraph);
        };

        if (!graph) {
            loadMap();
        }
    });

    const onNewMap = (newState: GameState) => {
        props.onNewMap(newState);

        // Force a reload of the map
        // Need to set the graph last, or the load can happen after the useEffect
        setMapUrl(undefined);
        setPathColor(undefined);
        setGraph(undefined);
        setCoverUrls([]);
    };

    if (!mapUrl || !graph || !pathColor) return <LoadingProgress />;

    return (
        <OverheadMap
            startState={startState}
            graph={graph}
            mapUrl={mapUrl}
            coverUrls={coverUrls}
            pathColor={pathColor}
            useDayNight={useDayNight}
            onNewMap={onNewMap}
        />
    );
};

export default OverheadMapLoader;
