import React from "react";
import { makeStyles } from "@material-ui/core";

import MockPartyService from "../../services/MockPartyService";
import Item from "../../models/Item";
import MockItemService from "../../services/MockItemService";
import ItemSelect from "./ItemSelect";

const useStyles = makeStyles({});

type StashProps = {};

const Stash: React.FunctionComponent<StashProps> = (props: StashProps) => {
    const classes = useStyles();

    const [stashItems, setStashItems] = React.useState<Item[] | null>(null);
    const [inventoryItems, setInventoryItems] = React.useState<Item[] | null>(null);

    React.useEffect(() => {
        // Initial load of inventory
        const loadItems = () => {
            // TODO: Try / catch
            const partyService = new MockPartyService();
            const stashItemIds = partyService.getStash();
            const inventoryItemIds = partyService.getInventory();

            const itemService = new MockItemService();
            const newStashItems = itemService.getItems(stashItemIds);
            const newInventoryItems = itemService.getItems(inventoryItemIds);

            setStashItems(newStashItems);
            setInventoryItems(newInventoryItems);
        };

        if (stashItems === null && inventoryItems === null) {
            loadItems();
        }
    });

    if (!stashItems || !inventoryItems) return null;

    const takeItem = (ids: string[]) => {
        const partyService = new MockPartyService();
        let newStashItems = stashItems.splice(0);
        const newInventoryItems = inventoryItems.splice(0);

        for (let i = 0; i < ids.length; i++) {
            const item = newStashItems.find((item) => item.id === ids[i]);
            if (!item)
                throw new Error("Item not found");

            newInventoryItems.push(item);
            newStashItems = newStashItems.filter(i => i.id !== item.id);
            partyService.removeStashItem(ids[i]);
            partyService.addInventoryItem(ids[i]);
        }

        setInventoryItems(newInventoryItems);
        setStashItems(newStashItems);
    };

    const putItem = (ids: string[]) => {
        const newStashItems = stashItems.splice(0);
        let newInventoryItems = inventoryItems.splice(0);

        const partyService = new MockPartyService();
        for (let i = 0; i < ids.length; i++) {
            const item = newInventoryItems.find((item) => item.id === ids[i]);
            if (!item)
                throw new Error("Item not found");

            newStashItems.push(item);
            newInventoryItems = newInventoryItems.filter((i) => i.id !== item.id);
            partyService.removeInventoryItem(ids[i]);
            partyService.addStashItem(ids[i]);
        }

        setInventoryItems(newInventoryItems);
        setStashItems(newStashItems);
    };

    return <ItemSelect items={stashItems} inventory={inventoryItems} useStash={true} onPut={putItem} onTake={takeItem} />;
};

export default Stash;
