import MockSaveService from "./MockSaveService";

export default class MockVariableService {
    getVariable(id: string): string | undefined {
        const saveService = new MockSaveService();
        return saveService.getObject<string>(id);
    }

    setVariable(id: string, value: string) {
        const saveService = new MockSaveService();
        saveService.saveObject<string>(id, value);
    }
}
