import React from "react";

import { Dialog, makeStyles } from "@material-ui/core";

import MockEncounterService from "../../services/MockStoryEncounterService";
import { Button, DialogActions, DialogContent } from "@material-ui/core";
import InkStoryChoice from "../../models/InkStoryChoice";
import PartySelect from "./PartySelect";
import HeroSelect from "./HeroSelect";
import HeroDismiss from "./HeroDismiss";
import Stash from "./Stash";
import StoryEncounterFinish from "../../models/InkStoryFinish";
import TraitSelect from "./TraitSelect";
import PartyInventorySelect from "./PartyInventorySelect";
import ItemLoot from "./ItemLoot";

const useStyles = makeStyles({
    paper: {
        overflow: "visible",
    },
    dialog: {
        // backgroundImage: "url(images/scroll.png)"
        userSelect: "none",
    },
    background: {
        position: "absolute",
        opacity: 0.1,
        right: "20px",
    },
    content: {
        minWidth: "520px",
        minHeight: "460px",
        padding: "40px",
        fontFamily: "Martel",
    },
    actions: {
        padding: "30px",
        //paddingBottom: "100px",
        //paddingRight: "80px",
    },
    portrait: {
        borderRadius: "50%",
        width: "200px",
        position: "absolute",
        top: "-60px",
        left: "-60px",
        border: "8px solid white",
    },
    placeholder: {
        float: "left",
        width: "130px",
        height: "140px",
    },
    paragraph: {
        display: "inline-block",
        width: "100%",
    },
});

type EncounterDialogProps = {
    onClose: (moveToNode?: string, startEncounterId?: string, coverVisible?: number) => void;
    encounterId: string | null;
};

const EncounterDialog: React.FunctionComponent<EncounterDialogProps> = (props: EncounterDialogProps) => {
    const { onClose, encounterId } = props;

    const [encounterService] = React.useState(new MockEncounterService());
    const [paragraphs, setParagraphs] = React.useState<string[]>([]);
    const [title, setTitle] = React.useState<string>("");
    const [choices, setChoices] = React.useState<InkStoryChoice[]>([]);
    const [storyStarted, setStoryStarted] = React.useState<boolean>(false);
    const [isReady, setIsReady] = React.useState<boolean>(false);

    const open: boolean = encounterId !== null;
    const classes = useStyles();

    const handleClose = () => {
        setStoryStarted(false);
        onClose();
    };

    const handleCloseWithValue = (moveTo?: string, encounterDetailId?: string, coverVisible?: number) => {
        setStoryStarted(false);
        onClose(moveTo, encounterDetailId, coverVisible);
    };

    const handleChoice = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        const choiceId = Number.parseInt(ev.currentTarget.id);

        // Tell the story where to go next
        const encounter = encounterService.chooseChoice(choiceId);

        if (!encounter.isFinished) {
            console.log(`Selected ${choiceId} for ${encounter.id}`);

            setParagraphs(encounter.paragraphText.split("..."));
            setTitle(encounter.title);
            setChoices(encounter.choices);
        } else {
            const encounterFinish = encounterService.continue();
            processFinish(encounterFinish.finishData);
        }
    };

    const handleContinue = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        const encounter = encounterService.continue();

        if (!encounter.isFinished) {
            setParagraphs(encounter.paragraphText.split("..."));
            setChoices(encounter.choices);
        } else {
            processFinish(encounter.finishData);
        }
    };

    const processFinish = (finish?: StoryEncounterFinish): void => {
        // Finished -> Check variables
        if (!finish || (finish.moveTo === undefined && finish.detailedEncounter === undefined && finish.coverVisible === undefined)) {
            handleClose();
        } else {
            handleCloseWithValue(finish.moveTo, finish.detailedEncounter, finish.coverVisible);
        }
    };

    React.useEffect(() => {
        // Initial load of encounter data
        const startEncounter = () => {
            if (!encounterId) return;

            console.log(`Loading encounter ${encounterId}`);

            setStoryStarted(true);

            const encounter = encounterService.startEncounter(encounterId);
            if (encounter.isFinished) {
                setParagraphs(["Error: Encounter finished"]);
                console.error(`No encounter data for ${encounterId}`);
                return;
            }
            console.log(`Started ${encounter.id} encounter`);
            setParagraphs(encounter.paragraphText.split("..."));
            setTitle(encounter.title);
            setChoices(encounter.choices);
        };

        // console.log(`useEffect2 ${encounterId} open=${open} storyStarted=${storyStarted}`);

        if (open && !storyStarted) {
            startEncounter();
        }
    }, [encounterId]);

    // Does the component disable the "continue" button until something chosen?
    let isBlockingDialog = false;

    const renderParagraph = (text: string) => {
        let paragraphText = text;

        const isHeroSwap = text.indexOf("[HeroSwap]") >= 0;
        if (isHeroSwap) {
            paragraphText = paragraphText.replace("[HeroSwap]", "");
            isBlockingDialog = true;
        }
        const isHeroSelect = text.indexOf("[HeroSelect]") >= 0;
        if (isHeroSelect) {
            paragraphText = paragraphText.replace("[HeroSelect]", "");
            isBlockingDialog = true;
        }
        const isHeroDismiss = text.indexOf("[HeroDismiss]") >= 0;
        if (isHeroDismiss) {
            paragraphText = paragraphText.replace("[HeroDismiss]", "");
            isBlockingDialog = false;
        }
        const isStash = text.indexOf("[Stash]") >= 0;
        if (isStash) {
            paragraphText = paragraphText.replace("[Stash]", "");
            isBlockingDialog = false;
        }
        const isInventory = text.indexOf("[Inventory]") >= 0;
        if (isInventory) {
            paragraphText = paragraphText.replace("[Inventory]", "");
            isBlockingDialog = true;
        }

        // Check for item select
        const selectItemsStart = text.indexOf("[Items(");
        const isSelectItems = selectItemsStart >= 0;

        let items: string[] | undefined = undefined;
        if (isSelectItems) {
            const selectItemsEnd = text.indexOf("]", selectItemsStart);
            const itemsStart = selectItemsStart + 7;
            const itemsRaw = paragraphText.substr(itemsStart, selectItemsEnd - itemsStart - 1);
            items = itemsRaw.split(",").map((t) => t.replaceAll('"', "").trim());
            paragraphText = paragraphText.substr(0, selectItemsStart).concat(paragraphText.substr(selectItemsEnd + 1));
            // isSpecialDialog = true;
        }

        // Check for trait select
        const selectTraitStart = text.indexOf("[SelectTrait(");
        const isSelectTrait = selectTraitStart >= 0;

        let traits: string[] | undefined = undefined;
        if (isSelectTrait) {
            const selectTraitEnd = text.indexOf("]", selectTraitStart);
            const traitsStart = selectTraitStart + 13;
            const traitsRaw = paragraphText.substr(traitsStart, selectTraitEnd - traitsStart - 1);
            traits = traitsRaw.split(",").map((t) => t.replaceAll('"', "").trim());
            paragraphText = paragraphText.substr(0, selectTraitStart).concat(paragraphText.substr(selectTraitEnd + 1));
            isBlockingDialog = true;
        }

        return (
            <div className={classes.paragraph}>
                <p>{paragraphText}</p>
                {isHeroSwap ? <PartySelect onReady={() => setIsReady(true)} /> : null}
                {isHeroSelect ? <HeroSelect onReady={() => setIsReady(true)} /> : null}
                {isHeroDismiss ? <HeroDismiss onReady={() => setIsReady(true)} /> : null}
                {traits && traits.length > 1 ? (
                    <TraitSelect from={traits[0]} onReady={() => setIsReady(true)} traitIds={traits.slice(1)} isRandomTrait={false} />
                ) : null}
                {items && items.length > 1 ? (
                    <ItemLoot onReady={() => setIsReady(true)} itemIds={items} isRandom={false} selectMultiple={false} />
                ) : null}
                {isStash ? <Stash /> : null}
                {isInventory ? <PartyInventorySelect onReady={() => setIsReady(true)} /> : null}
            </div>
        );
    }

    const renderChoices = (encChoices: InkStoryChoice[]) => {

        const disableContinue = !isReady && isBlockingDialog;

        if (encChoices.length === 0) {
            return (
                <Button onClick={handleContinue} disabled={disableContinue}>
                    Continue
                </Button>
            );
        }

        return encChoices.map((c: InkStoryChoice) => (
            <Button id={c.index.toString()} key={c.index} onClick={handleChoice}>
                {c.text}
            </Button>
        ));
    };

    return (
        <div>
            <Dialog
                classes={{
                    paper: classes.paper,
                }}
                className={classes.dialog}
                onClose={handleClose}
                aria-labelledby="simple-dialog-title"
                open={open}
            >
                <DialogContent className={classes.content}>
                    <img src="./images/knight.jpg" className={classes.portrait} alt="portrait" />
                    <div className={classes.placeholder}></div>
                    {/* <img className={classes.background} alt="" src="./images/chest.jpg" /> */}
                    <h1>{title}</h1>
                    {paragraphs.map((p) => renderParagraph(p))}
                </DialogContent>
                <DialogActions className={classes.actions}>{renderChoices(choices)}</DialogActions>
            </Dialog>
        </div>
    );
};

export default EncounterDialog;
