import GameState from "../models/GameState";

export default class TimeTracker {
    private static _dayLength: number = 24;

    public static isNight = (state: GameState): boolean => {
        const hour = state.currentTurn % TimeTracker._dayLength;
        return hour >= 15 && hour <= 20;
    };

    public static isMorning = (state: GameState): boolean => {
        const hour = state.currentTurn % TimeTracker._dayLength;
        return hour > 20;
    };

    public static isEvening = (state: GameState): boolean => {
        const hour = state.currentTurn % TimeTracker._dayLength;
        return hour > 11 && hour < 15;
    };

    public static toLogString = (state: GameState): string => {
        const hour = state.currentTurn % TimeTracker._dayLength;
        let logString = `Hour = ${hour} - `;
        if (TimeTracker.isNight(state)) logString += "It's night";
        else if (TimeTracker.isMorning(state)) logString += "It's morning";
        else if (TimeTracker.isEvening(state)) logString += "It's evening";
        else logString += "It's day";
        return logString;
    };
}
