import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";
import DoneIcon from "@material-ui/icons/Done";
import MockPartyService from "../../services/MockPartyService";
import HeroList from "../HeroList";

const useStyles = makeStyles({
    container: {
        position: "relative",
        height: "250px",
    },
    partyButton: {
        //margin: "auto 10px",
    },
    partyButtonDiv: {
        width: "100%",
        textAlign: "center",
    },
});

type PartySelectProps = {
    /** A valid selection has been made */
    onReady: () => void;
};

const PartySelect: React.FunctionComponent<PartySelectProps> = (props: PartySelectProps) => {
    const classes = useStyles();

    const [heroIds, setHeroIds] = React.useState<string[]>([]);
    const [isApplied, setIsApplied] = React.useState<boolean>(false);

    const selectionChanged = (ids: string[]) => {
        setHeroIds(ids);
    };

    const isValid = heroIds.length === 4;

    const changeParty = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isValid) return;

        const service = new MockPartyService();
        service.setPartyIds(heroIds);
        props.onReady();
        setIsApplied(true);
        // TODO: How to trigger a header reload?
        // TODO: Maybe a shared callback?
    };

    return (
        <div>
            <div className={classes.container}>
                <HeroList chooseAll={true} multiSelect={true} onSelectionChanged={isApplied ? undefined : selectionChanged} />
            </div>
            <Tooltip title={isApplied ? "Changes applied" : isValid ? "Apply party changes" : "Please select four heroes"}>
                <div className={classes.partyButtonDiv}>
                    <Button
                        className={classes.partyButton}
                        onClick={changeParty}
                        disabled={!isValid || isApplied}
                        variant="contained"
                        color="primary"
                        startIcon={<DoneIcon />}
                    >
                        {isApplied ? "Selected" : "Select"}
                    </Button>
                </div>
            </Tooltip>
        </div>
    );
};

export default PartySelect;
