import React from "react";
import { useHistory } from "react-router-dom";
import { Avatar, Card, CardContent, CardHeader, List, ListItem, ListItemAvatar, ListItemText, makeStyles } from "@material-ui/core";

import Quest from "../models/Quest";
import ExploreIcon from "@material-ui/icons/Explore";

import KeyMaps from "../components/KeyMaps";
import MockQuestService from "../services/MockQuestService";
import MockPartyService from "../services/MockPartyService";
import LinkTo from "../components/LinkTo";

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: "beige",
    },
    grid: {
        marginTop: "120px",
        fontFamily: "Martel",
        display: "grid",
        gridAutoRows: "minmax(100px, auto)",
        height: "76vh",
        [theme.breakpoints.down("md")]: {
            gridTemplateColumns: "repeat(1, 1fr)",
        },
        [theme.breakpoints.up("md")]: {
            gridTemplateColumns: "repeat(2, 1fr)",
        },
    },
    left: {},
    right: {
        //overflowY: "scroll",
        backgroundColor: "ivory",
        gridRowEnd: "span 2",
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
        margin: "30px",
    },
    close: {
        filter: "invert(42%) sepia(93%) saturate(1352%) hue-rotate(32deg) brightness(60%) contrast(119%)",
    },
    bannerContainer: {
        position: "relative",
        height: "15%",
        top: "-80px",
        zIndex: 1,
    },
    list: {
        padding: "20px",
    },
    item: {
        backgroundColor: "#fafaf0",
        margin: "10px",
        cursor: "pointer",
    },
    activeItem: {
        backgroundColor: "#ffffff",
        margin: "10px",
        cursor: "pointer",
    },
    questCard: {
        margin: "60px 20px",
        padding: "30px",
    },
    questContent: {
        fontFamily: "Martel",
    },
}));

type QuestsPageProps = {};

const QuestsPage: React.FunctionComponent<QuestsPageProps> = (props: QuestsPageProps) => {
    const classes = useStyles();

    const [quests, setQuests] = React.useState<Quest[] | undefined>(undefined);
    const [activeQuest, setActiveQuest] = React.useState<Quest | undefined>(undefined);

    const history = useHistory();
    const navigateToUrl = (url: string) => history.push(url);

    React.useEffect(() => {
        // Initial load of hero data
        const loadQuests = () => {
            const questService = new MockQuestService();
            const partyService = new MockPartyService();

            const partyQuests = partyService.getQuests();
            const quests: Quest[] = [];
            partyQuests.forEach((partyQuest) => {
                const quest = questService.getQuestDetail(partyQuest.id);
                quests.push(quest);
                if (partyQuest.status === "current") {
                    setActiveQuest(quest);
                }
            });
            setQuests(quests);
        };

        if (!quests) {
            loadQuests();
        }
    });

    if (quests === undefined) return null;

    const onSelectQuest = (selectedQuest: Quest) => setActiveQuest(selectedQuest);

    const renderQuests = (renderQuests: Quest[]): JSX.Element[] =>
        renderQuests.map((q) => (
            <ListItem className={activeQuest?.id === q.id ? classes.activeItem : classes.item} onClick={() => onSelectQuest(q)}>
                <ListItemAvatar>
                    <Avatar>
                        <ExploreIcon />
                    </Avatar>
                </ListItemAvatar>
                <ListItemText primary={q.name} secondary={q.description} />
            </ListItem>
        ));

    const renderActiveQuest = (): JSX.Element | null => {
        if (!activeQuest) return null;

        return (
            <Card className={classes.questCard}>
                <CardHeader title={activeQuest.name} />
                <CardContent className={classes.questContent}>{activeQuest.detail}</CardContent>
            </Card>
        );
    };

    return (
        <KeyMaps backUrl="/">
            <div className={classes.root}>
                <br />
                <LinkTo url={`${process.env.PUBLIC_URL}/`} top="10.2%" left="1%" width="40px">
                    <img src="images/close.svg" className={classes.close} alt="close" />
                </LinkTo>
                <div className={classes.bannerContainer}>
                    <h1 className="banner">Quests</h1>
                </div>
                <br />
                <div className={classes.grid}>
                    <div className={classes.left}>
                        <br />
                        <List className={classes.list}>{renderQuests(quests)}</List>
                    </div>
                    <div className={classes.right}>{renderActiveQuest()}</div>
                </div>
            </div>
        </KeyMaps>
    );
};

export default QuestsPage;
